.footer {
    width: 100%;   
    padding: 15px 0 28px 0;
    position: relative;
    z-index: 999;
    // .footer__container

    &__container {      
        @media (min-width: 650px){
            display: flex;
            justify-content: space-between;
        }
    }

    // .footer__column

    &__column {
        display: flex;
        align-items: center;  
        @media (max-width: 650px){
            justify-content: center;
            margin: 0 0 20px 0;
            &:last-child{
                margin: 0;
            }
        } 
        @media (max-width: 450px){
            &:first-child{
                flex-direction: column;
            }
        }    
    }

    &__phone{
        display: flex;
        align-items: center;
        &:hover{
            .icon-tel:before {
               color: #fff;
            }
            .footer__icon{
                background-color: #aa4141;
            }
           
        }
        @media (max-width: 450px){
            margin: 0 0 20px 0;
        }
        
    }
    // .footer__icon

    &__icon {
        margin: 0 10px 0 0;
        width: 30px;
        height: 30px;
        border: 1px solid #333333;
        position: relative;
        border-radius: 50%;
        transition: all 0.3s ease;
        .icon-tel:before {
            content: "\e909";
            color: #333;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        
    }

    // .footer__tel

    &__tel {
        display: inline-block;
        color: #333333;
        font-size: 18px;
        font-weight: 500;
        font-family: 'Roboto-Light';
        font-weight: 300;
        span{
            display: inline-block;
            font-family: 'Roboto-Medium';
            font-weight: 500;
        }
    }

    // .footer__social

    &__social {
    }

    // .footer__link

    &__link {
        display: block;
        cursor: pointer;
        color: #333333;
        font-weight: 300;
        margin: 0 15px 0 0;
        transition: all 0.3s ease;
        &:last-child{
            margin: 0;
        }
        &:hover{
            text-decoration: underline;;
        }
    }
}
.social {
    margin: 0 0 0 33px;
    @media (max-width: 450px){
        margin: 0;
    }
    // .social__facebook

    &__item {
        width: 32px;
        height: 32px;
        border: 1px solid #333333;
        display: inline-block;
        position: relative;
        margin: 0 8px 0 0;
        transition: all 0.3s ease;
        &:last-child{
            margin: 0;
        }
        &:hover{
            .icon-fb:before,
            .icon-vk:before,
            .icon-instagram:before {
                color: #fff;             
            }
            background-color: #aa4141;
        }
    }

    
    .icon-fb:before,
    .icon-vk:before,
    .icon-instagram:before {
        color: #333;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: all 0.3s ease;
    }
}
