.container { max-width: 1330px; margin: 0 auto; padding: 0 10px; min-width: 320px; }

.wrapper { min-height: 100%; display: flex; flex-direction: column; min-width: 320px; overflow: hidden; }

.wrapper__slider { position: relative; /* Лента слайдов */ }

.wrapper__slider .slick-list { overflow: hidden; }

