.header__logo { position: relative; z-index: 4; display: flex; justify-content: center; align-items: center; }

.header__logo .icon-logo { color: #333333; font-size: 58px; }

.header__logo span { display: inline-block; color: #333333; font-family: 'MuseoSans'; font-size: 22px; font-weight: 900; line-height: 29px; text-transform: uppercase; margin: 0 0 0 5px; white-space: nowrap; }

