@media (max-width: 992px) { .header__logo { position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%); } }

@media (max-width: 992px) { .icon-menu { position: relative; width: 30px; height: 19px; cursor: pointer; z-index: 5; }
  .icon-menu span { width: 100%; height: 3px; background-color: #333; position: absolute; top: 8px; left: 0; }
  .icon-menu span:first-child { top: 0; transition: all 0.3s ease; }
  .icon-menu span:last-child { top: auto; bottom: 0; transition: all 0.3s ease; }
  .icon-menu.active span { transform: scale(0); }
  .icon-menu.active span:first-child { transform: rotate(-45deg); top: 8px; }
  .icon-menu.active span:last-child { transform: rotate(45deg); bottom: 8px; } }

.menu { flex: 1 1 auto; padding: 0 0 0 57px; }

@media (max-width: 1260px) { .menu { padding: 0; } }

@media (max-width: 992px) { .menu { flex: 0 0 auto; } }

.menu.active .menu__body { opacity: 1; visibility: visible; }

@media (max-width: 992px) { .menu__body { background-color: #bb9c66; position: fixed; width: 100%; height: 100%; left: 0; top: 0; overflow: auto; padding: 120px 0 40px 0; opacity: 0; visibility: hidden; transition: all 0.3s ease; z-index: 3; }
  .menu__body.active { opacity: 1; visibility: visible; }
