  .menu__list li:last-child { margin: 0; } }

@media (max-width: 1260px) { .menu__list { justify-content: center; } }

@media (max-width: 992px) { .menu__list { display: block; text-align: center; }
  .menu__list li { margin: 0 0 20px 0; }
  .menu__list li:last-child { margin: 0 0 20px 0; } }

.menu__link { color: #333333; font-size: 18px; font-weight: 300; transition: all 0.3s ease; white-space: nowrap; }

@media (min-width: 992px) { .menu__link:hover { text-decoration: underline; } }

@media (max-width: 992px) { .menu__link { font-size: 22px; } }

.actions-header { display: flex; align-items: center; }

@media (max-width: 550px) { .actions-header { justify-content: center; } }

.actions-header__user { margin: 0px 0px 0px 30px; }

@media (max-width: 1310px) { .actions-header__user { margin: 0px 0px 0px 20px; } }

@media (max-width: 550px) { .actions-header__user { margin: 0; } }

.user-header { display: flex; }

.user-header__menu { display: flex; }

.user-header__menu li { list-style: none; margin: 0 15px 0 0; }

.user-header__menu li:last-child { margin: 0; }

@media (min-width: 990px) { .user-header__menu li:hover { text-decoration: underline; } }

@media (max-width: 1260px) { .user-header .user-header__menu:last-child { display: none; } }

@media (max-width: 550px) { .user-header { flex-direction: column; align-items: center; text-align: center; }
