

.header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    @media (max-width: 992px){
        &.fixed{
            position: fixed;
            background-color: #fff;
        }
    }
    
    &__container{
        max-width: 1330px;
        min-width: 320px;
        overflow-x: hidden;
        padding: 20px 10px;
        display: flex;
        align-items: center;
        position: relative;
        @media (max-width: 992px){
            justify-content: space-between;
        }
    }
    &__logo {
        position: relative;
        z-index: 4;
        display: flex;
        justify-content: center;
        align-items: center;
        .icon-logo{
            color: #333333;
            font-size: 58px;
        }
        span{
            display: inline-block;
            color: #333333;
            font-family: 'MuseoSans';
            font-size: 22px;
            font-weight: 900;
            line-height: 29px;
            text-transform: uppercase;
            margin: 0 0 0 5px;
            white-space: nowrap;
        }
        @media (max-width: 380px){
            span{
                display: none;
            }
        }
        @media (max-width: 992px){
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }

    // .header__menu

    &__menu {
    }

    // .header__actions

    &__actions {
    }
}
.icon-menu {
    @media (max-width: 992px){
        position: relative;
        width: 30px;
        height: 19px;
        cursor: pointer;
        z-index: 5;
        span{
            width: 100%;
            height: 3px;
            background-color: #333;
            position: absolute;
            top: 8px;
            left: 0;
            &:first-child{top: 0;transition: all 0.3s ease;}
            &:last-child{top: auto; bottom: 0;transition: all 0.3s ease;}
        }
        &.active{
            span{
                transform: scale(0);
                &:first-child{transform: rotate(-45deg); top:8px;}
                &:last-child{transform: rotate(45deg); bottom:8px;}
            }
        }
    }
        
}
.menu {
    flex:1 1 auto;
    padding: 0 0 0 57px;
    @media (max-width: 1260px){
        padding: 0;
    }
    @media (max-width: 992px){
        flex: 0 0 auto;
    }
    &.active{
        .menu__body{
            opacity: 1;
            visibility: visible;
        }  
    }
    &__body {
        @media (max-width: 992px){
                background-color: rgba(187, 156, 102, 1);
                position: fixed;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;  
                overflow: auto;             
                padding: 120px 0 40px 0;
                opacity: 0;
                visibility: hidden;
                transition: all 0.3s ease;
                z-index: 3;
                &.active{
                    opacity: 1;
                    visibility: visible;
                }
            &::after{
                content: '';
                position: fixed;
                top: 0;
                left: 0;              
                background-color: #fff;
                height: 65px;
                width: 100%;              
            }
        }       
    }

    // .menu__list

    &__list {
        display: flex;
        li{
            margin: 0 30px 0 0;
            list-style: none;
            &:last-child{
                margin: 0;
            }
        }
        @media (max-width: 1310px){
            li{
                margin: 0 20px 0 0;
                &:last-child{
                    margin: 0;
                }
            } 
        }
        @media (max-width: 1260px){
            justify-content: center;
        }
        @media (max-width: 992px){
            display: block;
            text-align: center;
            li{
                margin: 0 0 20px 0;
                &:last-child{
                    margin: 0 0 20px 0;
                }
            }
        }
        
    }

    // .menu__link

    &__link { 
        color: #333333;      
        font-size: 18px;
        font-weight: 300;   
        transition: all 0.3s ease;   
        white-space: nowrap; 
        @media (min-width: 992px){
            &:hover{
                text-decoration: underline;
            }
        }
        @media (max-width: 992px){
            font-size: 22px;
        }
    }
}
.actions-header {
    display: flex;
    align-items: center;
    @media (max-width: 550px){
        justify-content: center;
    }
    // .actions-header__user

    &__user {
        margin: 0px 0px 0px 30px;
        @media (max-width: 1310px){
            margin: 0px 0px 0px 20px;
        }
        @media (max-width: 550px){
            margin: 0;
        }
    }
}
.user-header {
    display: flex;
    &__menu {
        display: flex; 
        li{
            list-style: none;
            margin: 0 15px 0 0;
            &:last-child{
                margin: 0;
            }
            @media (min-width: 990px){
                &:hover{
                    text-decoration: underline;
                }
            }
            
        }
    }
    @media (max-width: 1260px){
        .user-header__menu{
            &:last-child{
                display: none;
            }
        }     
    }  
    @media (max-width: 550px){
        flex-direction: column;
        align-items: center;
        text-align: center;
        .user-header__menu{
            li{
                margin: 0 0 20px 0;
            }
            &:first-child{
                order: 2;
            }
            &:last-child{
                display:flex;
                flex-direction: column;
                order: 1;
            }
        }  
        .user-header__link{
            font-size: 22px;
        }
    }
    // .user-header__link

    &__link {
        color: #333333;
        font-size: 14px;
        font-weight: 300;
        line-height: 29px;       
    }

    // .user-header__link1

   
}

.lang-menu {
    margin: 0 48px 0 0;
    position: relative;
    z-index: 4;
    li{
        list-style: none;
        margin: 0;
    }
    // .lang-menu__link

    &__link {
        color: #333333;
        font-size: 14px;
        font-weight: 300;
        line-height: 29px;
        padding: 5px 7px;
        transition: all 0.3s ease;
        &.active{
            border: 1px solid #333333;
        }
        @media (max-width: 550px){
            font-size: 20px;
        }
    }
    @media (max-width: 1310px){
        margin: 0 38px 0 0;
    }
    @media (max-width: 992px){
        margin: 0;
    }
}



