.page {
    flex: 1 1 auto;
    position: relative;
    z-index: 999;
    // .page__container

    &__container {
        padding: 115px 10px 10px 10px;  
        @media (min-width: 992px){
            display: flex;
        }
        @media (max-width: 650px){
            padding: 100px 10px 0 10px;
        }
    }

      // .page__information

      &__information {
          flex: 0 1 50%;
    }

    // .page__order

    &__order {
        flex: 0 1 50%;
    }

}

.information {

    // .information__body

    &__body {
    }

    // .information__text

    &__text {
        font-size: 15px;
        line-height: 20px;
        font-family: 'Roboto-Light';
        margin: 0 0 40px 0;
        color: #333333;
        p{
            margin: 0 0 25px 0;
            &:last-child{
                margin: 0;
            }
        }
        &_a{
            display: inline-block;
            cursor: pointer;
            transition: all 0.3s ease;
            font-family: 'Roboto-Medium';
            color: #333333;
            &:hover{
                text-decoration: underline;
            }
        }
    }

    // .information__title

    &__title {
        color: #333333;
        font-size: 16px;
        font-weight: 900;
        line-height: 29.34px;
        margin: 0 0 22px 0;
    }

    // .information__rooms

    &__rooms {
    }
}
.rooms {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;
    padding: 0 50px 0 0;
    @media (max-width: 992px){
        padding: 0;
        margin: 0 0 30px 0;
    }
    @media (max-width: 1250px){
        padding: 0;
    }
    // .rooms__column

    &__column {
        flex: 0 1 33.333%;
        padding:0 10px;
        margin: 0 0 17px 0;
        &:hover{
            .rooms__image{
                img{
                    transform: scale(1.13);
                }
                &:before{
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
        @media (max-width: 768px){
            flex: 0 1 50%;
        }
        @media (max-width: 500px){
            flex: 1 1 100%;
            padding: 0;
        }
    }
    // .rooms__image

    &__image {
        display: block;
        overflow: hidden;
        position: relative;
        transition: all 0.5s ease;
        cursor: pointer;
        &:before{
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba(3, 1, 1, 0.25);
            opacity: 0;
            visibility: hidden;
            z-index: 2;
        }
        img{
            width: 100%;
            object-fit: cover;
            transition: all 0.4s ease;
            position: relative;    
        }
    }

    // .rooms__text

    &__text {
        display: flex;
        align-items: center;
        color: #333333;
        font-weight: 300;
        margin: 9px 0 0 0;
        span{
            display: flex;
            align-items: center;
            margin: 0 6px 0 0;
            .icon-people_1:before,
            .icon-people_2:before{
                font-size: 23px;
                color: #333;
            }       
        }
        @media (max-width: 500px){
            justify-content: center;
        }

    }
}

