.order {
   

    &__body{
        padding: 0 0 0 70px;
        @media (max-width: 1250px){
            padding: 0 0 0 40px;
        }
        @media (max-width: 992px){
            margin: 0 0 50px 0;
            padding: 0;
        }

    }
    // .order__first

    &__first {
        max-width: 490px;
    }

    // .order__second

    &__second {
    }
}


.first-order {
    overflow: hidden;
    margin: 0 0 60px 0;
    @media (max-width: 992px){
        margin: 0 auto 60px auto;
    }

    .jq-selectbox__select {
        padding: 10px 40px 0 15px;
        border: 1px solid #9d9d9d;
        border-radius: 3px;
        background: #fff;
    }
    // .first-order__item

    &__item {
        display: flex;
        margin: 0 0 15px 0;
        &:last-child{
            margin: 0;
        }
        &_short{
            flex: 0 0 325px;
            width: 325px;
            @media (max-width: 768px){
                flex: 0 0 auto;
                width: auto;
            }
           
        }
        @media (max-width: 450px){
            &:nth-child(2){
                flex-direction: column;
                margin: 0 0 15px 0;
            }
            &:nth-child(3){
                flex-direction: column;
            }
        }  
    }

    // .first-order__side

    &__side {
        flex: 0 0 110px;
        width: 110px;
        display: flex;
        align-items: center;
        &_center{
            justify-content: center;
            flex: 0 0 87px;
            width: 87px;
            &_2{
                flex: 0 0 125px;
                width: 125px;
            }
            @media (max-width: 450px){
                flex: 0 0 110px;
                justify-content: flex-start;
            } 
        }
    }

    // .first-order__content

    &__content {
        flex: 1 1 auto;
        height: 36px;
        cursor: pointer;
        .input-text{
            width: 100%;
            color: #333333;
            font-size: 16px;
            font-weight: 400;
            height: 100%;
            padding: 0 10px;
            border: 1px solid #9d9d9d;
            
        }
        &_short{
            height: 36px;
            flex: 1 1 auto;
            
           
        }
        &_first{    
            height: 45px;       
            .jq-selectbox__select-text{
                font-size: 16px;
                position: relative;
            }
            .jq-selectbox__select {
                position: relative;
                display: flex;         
                &:before{
                    content: '\e907';
                    font-size: 30px;                   
                    left: 0; 
                    font-family: 'icomoon';
                }
            }
        }
        &_calendar{           
            height: 36px; 
            position: relative; 
            .icon-date{
                font-size: 24px;
                position: absolute;
                right: 5px;
                top: 50%;
                transform: translate(0, -50%);
            }          
        }
       
    }

    &__option{
        
    }

    &__column{
        display: flex;
        flex: 1 1 auto;
        @media (max-width: 450px){
            margin: 0 0 15px 0;
            &:last-child{
                margin: 0;
            }
        }
    }
     // .order__second

     &__second {

    }
    .jq-selectbox li.selected {
        background-color: #D0C5BE;
        color: #FFF;
    }
    
    .jq-selectbox li:hover {
        background-color: #EAE7DF;
        color: #000;
    }
    .jq-selectbox__dropdown{
        border: 1px solid #9d9d9d;
    }
}

.second-order {

    
    @media (max-width: 992px){
        text-align: center;
    }
    // .second-order__title
    &__title {
        margin: 0 0 24px 0;
        color: #333333;
        font-size: 16px;
        font-weight: 900;
        line-height: 29px;
        text-transform: uppercase;
    }

    // .second-order__check

    &__check {
        margin: 0 0 45px 0; 
        display: flex; 
        @media (max-width: 992px){
            display: block;
        }    
    }


    // .second-order__rezult

    &__rezult {
        color: #333333;
        font-size: 24px;
        font-weight: 500;
        line-height: 29px;
        margin: 0 0 18px 0;
        span{
            font-family: 'Roboto-Medium';
            font-weight: 500;
        }
    }

    // .second-order__button

    &__button {
    }
}
.checkbox {
    @media (min-width: 992px){
        cursor: pointer;
        user-select: none;
        white-space: nowrap;
    }
    @media (max-width: 500px){
        display: block;
        margin: 0 0 30px 0;
    }
    // .checkbox__input

    &__input {
    }

    // .checkbox__text

    &__text {
        position: relative;
        padding: 10px 10px 10px 30px;
        &:before{
            content: '';
            width: 10px;
            height: 10px;
            position: absolute;
            background-image: url('../img/icons/krug.png');
            left: 10px;
            top: 55%;
            transform: translate(0, -50%);
        }
        
    }
}
.checkbox__input:checked + .second-order__checkbox{
    background-color: #aa4141;
}
.checkbox__input:checked + .checkbox__text:before{  
        content: '';
        width: 14px;
        height: 10px;
        background-image: url('../img/icons/check.png');
}
.checkbox__input:checked + .checkbox__text{  
    color: #fff;
    background-color: #aa4141;
}
.btn {
    background-color: #642121;
    width: 180px;
    padding: 20px 0;
    text-align: center;
    position: relative;   
    overflow: hidden;
    cursor: pointer;
    @media (max-width: 992px){
        margin: 0 auto;
    }
    span{
        display: inline-block;
        color: #ffffff;
        font-size: 18px;
        font-weight: 700;
        text-transform: uppercase;
        z-index: 3;
        position: relative;
    }
    &:after{
        content: '';
        position: absolute;
        top: -100%;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #aa4141;
        transition: all 0.3s ease;
    }
    &:hover{
        &:after{
            top: 0;
        }
    }
}
.jq-selectbox__trigger-arrow {
    position: absolute;
    top: 0px;
    right: 0px;
    width: 15px;
    height: 100%;
    overflow: hidden;   
    background: none;
}

.jq-selectbox__trigger-arrow {
    position: absolute;
    background: url('../img/icons/arrow.png');
    top: 50%;
    left: 11px;
    transform: translate(0, -50%);
    width: 12px;
    height: 7px;
    border: none;

}




